     <el-row>
        <el-table :data="testcaseList" style="width: 100%" border>
          <el-table-column prop="id" label="编号" min-width="100">
            <template scope="scope"> {{ scope.row.pk }} </template>
          </el-table-column>
          <el-table-column prop="case_name" label="用例名" min-width="100">
            <template scope="scope"> {{ scope.row.fields.case_name }} </template>
          </el-table-column>
          <el-table-column prop="add_time" label="添加时间" min-width="100">
            <template scope="scope"> {{ scope.row.fields.add_time }} </template>
          </el-table-column>
          <el-table-column prop="add_time" label="操作" min-width="100">
            <template> <a href="/#/workstation"><i class="uk-icon-edit"></i>edit</a> </template>
          </el-table-column>
        </el-table>
    </el-row> 
    <div>
        <div class="uk-grid">
            <div style="background-color: #FFC1C1" class="uk-width-large-1-1 uk-visible-medium">112</div>
        <!-- <div class="uk-width-1-2">3344</div> -->
    </div>
