  </div>
  </div>
</template>

<script>
export default {
  name: 'workstation',
  data () {
    return {
      msg: 'Vue + Test Case Library',
      input: '',
      testcaseList: [],
    }
  },
  mounted: function() {
      this.getTestCases()
  },
  methods: {
      createTestCaseByName(){
      this.$http.get('http://127.0.0.1:8000/api/add_testcase?case_name=' + this.input)
        .then((response) => {
            var res = JSON.parse(response.bodyText)
            if (res.error_num == 0) {
              this.getTestCases()
            } else {
              this.$message.error('新增用例失败，请重试')
              console.log(res['msg'])
            }
        })
    },
    createTestCase(){
      this.$http.get('http://127.0.0.1:8000/api/add_testcase?case_name=' + this.input)
        .then((response) => {
            var res = JSON.parse(response.bodyText)
            if (res.error_num == 0) {
              this.getTestCases()
            } else {
              this.$message.error('新增用例失败，请重试')
              console.log(res['msg'])
            }
        })
    },
    getTestCases(){
      this.$http.get('http://127.0.0.1:8000/api/get_testcases')
        .then((response) => {
            var res = JSON.parse(response.bodyText)
            console.log(res)
            if (res.error_num == 0) {
              this.testcaseList = res['list']
            } else {
              this.$message.error('查询用例失败')
              console.log(res['msg'])
            }
        })
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
