<template>
  <div class="caseworkstation">
    <h1>测试用例：  {{ testcase }}</h1>
    <h1>测试：  {{ message }}</h1>
    <h1>测试：  {{ fortest }}</h1>
    <!-- <h1>{{ model }}</h1> -->
    <!-- <h1>{{ Object.keys(model[0]) }}</h1> -->
      <ul id="example-1" class="uk-">
        <div class="uk-grid">
        <a href="#url-model-id" data-uk-offcanvas>...</a>
        <div class="uk-width-medium-1-6">
              <div data-uk-modal="{target:'#url-model-id'}" class="uk-panel-title uk-panel-box uk-panel-box-primary uk-panel-space uk-panel-divider ">
                <i class="uk-icon-sign-in"></i>
                web url
              </div>
				</div>

        <li v-for="item in model" v-bind:key="item.fields.step_name ">
				     <div class="uk-width-medium-1-1">
                  <div @click="getModel(item.fields.step_name)" class="uk-panel-title uk-panel-box uk-panel-box-primary uk-panel-space uk-panel-divider ">
                    <i class="uk-icon-edit"></i>
                    {{ item.fields.step_name }}
                  </div>  
              </div>
        </li>
        <div class="uk-width-medium-1-6">
            <div v-on:click="addCard" class="uk-panel-title uk-panel-box uk-panel-box-primary uk-panel-space uk-panel-divider ">
              <i class="uk-icon-plus"></i>
            </div>
         </div>
        </div>
      </ul>
    <div id="app-2">
  <span v-bind:title="message">
    鼠标悬停几秒钟查看此处动态绑定的提示信息！
  </span>
</div>


<!-- 模态对话框 web url entrance -->
<div id="url-model-id" class="uk-modal">
    <div class="uk-modal-dialog">
        <a class="uk-modal-close uk-close"></a>
        <form class="uk-form uk-form-stacked">
          <fieldset data-uk-margin>
            <legend>web 入口</legend>
            <div class="uk-form-colume uk-form-horizontal">
              <li v-for="item in Object.keys(url_model)" v-bind:key="item">
                  <label class="uk-form-label">{{item}}</label>
                  <input type="text" placeholder="99999" class="uk-form-width-medium">
              </li>
            </div>
        </fieldset>
      <button class="uk-button uk-button-primary" type="button" data-uk-button>确认</button>
      <button class="uk-button" type="button" data-uk-button>取消</button>
      </form>
    </div>
</div>

<!-- 模态对话框  case step entrance -->
<div id="step-model-id" class="uk-modal">
    <div class="uk-modal-dialog">
        <a class="uk-modal-close uk-close"></a>
        <form class="uk-form uk-form-stacked">
          <fieldset data-uk-margin>
            <legend>创建测试步骤</legend>
            <div class="uk-form-colume uk-form-horizontal">
              <li v-for="item in Object.keys(step_model)" v-bind:key="item">
                  <label class="uk-form-label">{{item}}</label>
                  <input type="text" placeholder="99999" class="uk-form-width-medium">
              </li>
            </div>
        </fieldset>
        <tr></tr>
      <button class="uk-button uk-button-primary" type="button" data-uk-button>确认</button>
      <button class="uk-button" type="button" data-uk-button>取消</button>
      </form>
    </div>
</div>

<!-- 模态对话框  el dialog -->
<el-dialog
  title="用例步骤"
  :visible.sync="modeldialogVisible"
  width="30%"
  :before-close="handleClose">
  <li v-for="item in Object.keys(step_model)" v-bind:key="item">
      <div class="demo-input-suffix">
              <el-label>{{item}}：</el-label>
              <el-input
                placeholder="请输入"
                v-model="input">{{step_model.item}}
              </el-input>
      </div>
  </li>
  <span slot="footer" class="dialog-footer">
    <el-button @click="modeldialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="modeldialogVisible = false">确 定</el-button>
  </span>
</el-dialog>

</div>
</template>
<script>
export default {
  name: 'caseworkstation',
  // el: '#app-2',
  data () {
    return {
      msg: 'Vue + caseworkstation',
      message: '页面加载于 ' + new Date().toLocaleString(),
      testcase: "default",
      input: '',
      testcaseList: [],
      modeldialogVisible: false,
      items: [],
      // model:[],
      step_model_list:[],
      fortest:"defaulttest",
      url_model:{
        "url":""
        },
      step_model:{
        "identifier":"",
        "location":"",
        "type":"",
        "ops":"",
        "value":"",
        "reg_type":"",
        "ret_assert":"",
        "required":1,
        "imgdiff":0,
      },
    }
  },
  mounted: function() {
      this.getTestCase()
      this.getSheetModel()
      
  },
  methods: {
      getSheetModel: function(){
        this.testcase = this.msg.split("=")[1]
        this.$http.get('http://127.0.0.1:8000/api/get_testcasesteps?case_name=test1')
        .then((response) => {
            var res = JSON.parse(response.bodyText)
            if (res.error_num == 0) {
              this.model = res.list
              debugger;
            } else {
              this.$message.error('新增用例失败，请重试')
              console.log(res['msg'])
            }
        })
      },
      getTestCase: function(){
                this.msg = window.location.href
            },
      inputWebsite: function(){
                var myDate = new Date();
                this.msg = "456"; //+ formatDate(date, 'yyyy-MM-dd hh:mm');
            },
      addCard: function(){
                // debugger;
                this.model.push({ fields: {"step_name":"new"}} )
            },
      getModel: function(e){
                // this.message = "oh yeah"+ e.target.innerText
                this.getSheetModel()
                this.modeldialogVisible = true
                this.message = "oh yeah:"+ e
                this.fortest = "fortest:"+this.model
                debugger;
                for (var item in this.model){
                  this.fortest = "fortest1:"+ item.fields.step_name
                  if(item.fields.step_name == e){
                    this.step_model.identifier = item.fields.locator
                    this.step_model.location = item.fields.location
                    this.step_model.type = item.fields.type
                    this.step_model.ops = item.fields.ops
                    this.step_model.value = item.fields.value
                    this.step_model.reg_type = item.fields.reg_type
                    this.step_model.ret_assert = item.fields.ret_assert
                    this.step_model.required = item.fields.required
                    this.step_model.imgdiff = item.fields.imgdiff
                    this.fortest = "fortest:"+ this.step_model
                  }
                  else{
                    this.fortest = "oh yeah:"+ "error"
                  }
                }
            },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
} 
div.uk-panel-box:hover  {background-color:rgb(149, 214, 245);font-size: 30px;}
</style>



